<template>
  <div class="users">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
     </el-breadcrumb>

      <!--    -->
    <el-card >
       <!--  -->
        <el-row :gutter="20">
            <el-col :span="9">
                 <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList"> 
                    <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4">
                <el-button type="primary" @click="addDialogVisible=true"></el-button>
            </el-col>
        </el-row>

        <!--  -->
        <el-table :data="userList" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="username"></el-table-column>
            <el-table-column label="" prop="email"></el-table-column>
            <el-table-column label="" prop="mobile"></el-table-column>
            <el-table-column label="" prop="role_name"></el-table-column>
             <el-table-column label="">
                 <template slot-scope="scope">
                    <!-- {{scope.row}} -->
                    <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
                 </template>
             </el-table-column>
            <el-table-column label="" width="190">
                <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserByid(scope.row.id)"></el-button>
                        <!--  -->
                         <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini" @click="ss(scope.row)"></el-button>
                        </el-tooltip>
                       
                </template>
            </el-table-column>
            
        </el-table>

        <!--  -->
         <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1,2,5,10]"
            :page-size="queryInfo.pagesize"
            :total="total"
            layout="total,sizes,prev,pager,next,jumper">
        </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
        title=""
        :visible.sync="addDialogVisible "
        width="50%"
        @close="addDialogClosed"
        >
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px" class="demo-ruleForm">
            <el-form-item label=" " prop="username">
                <el-input v-model="addForm.username"></el-input>
            </el-form-item>
             <el-form-item label=" " prop="password">
                <el-input v-model="addForm.password"></el-input>
            </el-form-item>
             <el-form-item label=" " prop="email">
                <el-input v-model="addForm.email"></el-input>
            </el-form-item>
             <el-form-item label=" " prop="mobile">
                <el-input v-model="addForm.mobile"></el-input>
            </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addUser"> </el-button>
        </span>
    </el-dialog>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="editDialogVisidle"
  width="50%"
  @close="editDialogClosed"
 >
<el-form :model="editForm" :rules="editFormRules " ref="editFormRef" label-width="70px" >
  <el-form-item label="" >
    <el-input v-model="editForm.username" disabled></el-input>
  </el-form-item>
    <el-form-item label=""  prop="email">
    <el-input v-model="editForm.email" ></el-input>
  </el-form-item>
    <el-form-item  label="" prop="mobile" >
    <el-input v-model="editForm.mobile" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisidle = false"> </el-button>
    <el-button type="primary" @click="editUserinfo"> </el-button>
  </span>
</el-dialog>

<!--  -->
<el-dialog
  title=""
  :visible.sync="setRoleDialogVisible"
  width="50%"
>
    <div>
        <p>{{userInfo.username}}</p>
        <p>{{userInfo.role_name}}</p>
        <p>
              
            <el-select v-model="selectedRouleid" placeholder="">
                <el-option
      v-for="item in roleslist"
      :key="item.id"
      :label="item.roleName"
      :value="item.id">
    </el-option>
  </el-select>
        </p>
    </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRoleDialogVisible = false"> </el-button>
    <el-button type="primary" @click="gengai"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
name:"",
  components: {},
  props: {},
  data() {
      //
      var checkEmail = (rule,value,cb)=>{
          const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
            if(regEmail.test(value)){
                return cb()
            }
            cb(new Error(""))

      }

 
    //
      var checkMobile = (rule,value,cb)=>{
          const regMobile=/^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
          if(regMobile.test(value)){
              return cb()
          }
          cb(new Error(""))
      }
    return {
        //
        queryInfo:{
            //
            query:'',
            // 
            pagenum:1,
            // 
            pagesize:2
        },
        setRoleDialogVisible:false,
        userList:[],
        roleslist:[],
        //
        editDialogVisidle:false,
        editForm:{},
        userInfo:{},
selectedRouleid:"",
        editFormRules:{
        email:[
     {required:true,message:'',trigger:'blur'},
     {
         validator:checkEmail,trigger:"blur"
     }
     
     ],
     mobile:[
    {required:true,message:'',trigger:'blur'},
     {
         validator:checkMobile,trigger:"blur"
     },
]
        },
        total:0,
        // 
        addDialogVisible:false,
        // 
        addForm:{
            username:'',
            password:'',
            email:'',
            mobile:''
        },
        //
        addFormRules:{
            username:[
                {required:true,message:'',trigger:'blur'},
                {min:3,max:10,message:'3~10',trigger:'blur'}
            ],
            password:[
                {required:true,message:'',trigger:'blur'},
                {min:6,max:15,message:'6~15',trigger:'blur'}
            ],
             email:[
                {required:true,message:'',trigger:'blur'},{validator:checkEmail,trigger:"blur"}
            ],
            mobile:[
                {required:true,message:'',trigger:'blur'},{validator:checkMobile,trigger:"blur"}
            ]
        }
    };
  },
  watch: {},
  computed: {},
  methods: {
       //
     async getUserList(){
          let {data:res} = await this.$http.get('users',
          {params:this.queryInfo})
          console.log(res)
          if(res.meta.status !==200){
              return this.$message.error("")
          }else{
              this.userList=res.data.users
              this.total = res.data.total
          }
      },
        //
      addDialogClosed(){
          this.$refs.addFormRef.resetFields()
      },
      //
      handleSizeChange(newSize){
        //   console.log(newSize)
          this.queryInfo.pagesize=newSize
          this.getUserList();
      },
      //
      handleCurrentChange(newPage){
          this.queryInfo.pagenum=newPage
          this.getUserList();
        //   console.log(newPage)
      },
      //switch
      async userStateChanged(userinfo){
          let {data:res} = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
          console.log(res)
          if(res.meta.status !==200){
              userinfo.mg_state=!userinfo.mg_state
              return this.$message.error('')
          }else{
              this.$message.success("!")
          }
      },
      addUser(){
          this.$refs.addFormRef.validate(async valid=>{
              console.log(valid)
               if(!valid) return
               //
            const {data:res}= await this.$http.post("users",this.addForm)
            if(res.meta.status !==201){
                this.$message.error("")

            }
            this.$message.success("")
            this.addDialogVisible=false
            this.getUserList()
          })
      },
      //
         async showEditDialog(e){
          const {data:res} =await this.$http.get('users/'+e)
          if(res.meta.status !==200){ 
              return this.$message.error("") 
          }
           this.editForm=res.data;
          this.editDialogVisidle=true
         
    console.log(editForm)
      },
     async ss(userInfo){
         
          this.userInfo=userInfo
          const {data:res}= await this.$http.get("roles")
          if(res.meta.status !==200){
              return this.$message.error("")
          }
          this.roleslist=res.data
              this.setRoleDialogVisible=true
          console.log(  this.roleslist)
    
      },
      //
      editDialogClosed(){
            this.$refs.editFormRef.resetFields()
      },
      
      editUserinfo(){
              this.$refs.editFormRef.validate(async valid=>{
              console.log(valid) 
                  if(!valid) return 
                     //
            const {data:res}= await this.$http.put("users/"+this.editForm.id,{
                email:this.editForm.email,
                mobile:this.editForm.mobile
            })
            if(res.meta.status !==200){
                return this.$message.error("")

            }
            //
            this.editDialogVisidle=false
            //
            this.getUserList()
            this.$message.success("")

              })
           
           
      },
     async removeUserByid(id){
       const res= await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err=>err)
        // console.log(res)
        if(res !=="confirm"){
            return this.$message.info("")
        }
        const {data:res1}=await this.$http.delete("users/"+id)
        if(res1.meta.status !==200){
            return this.$message.error("")
        }
        this.$message.success("")
               this.getUserList()

      },
     async gengai(){
          if(!this.selectedRouleid){
              return this.$message.error("")
          }
        const {data:res}= await this.$http.put(`users/${this.userInfo.id}/role`,{
              rid:this.selectedRouleid
          })

          if(res.meta.status !==200){
              return this.$message.error("")
          }
          this.$message.success("")
          this.getUserList()
          this.setRoleDialogVisible=false
      }
  },
  created() {
      //
      this.getUserList()
  },
  mounted() {
     
  }
};
</script>
<style lang="scss" scoped>
.users{
    width: 100%;
    height: 100%;
    .el-table{
        margin-top: 15px;
    }
}
</style>